<template>
  <div>
    <TemplatePlayGround
        :header-nav="headerNav"
        :template-name="sectionName"
        :template-title="sectionTitle"
        :template-description="sectionDescription">
    </TemplatePlayGround>

    <!--region Main Content-->
    <section class="blog">
      <div class="container align-self-center">

        <!-- Chart -->
        <div class="row">
          <!-- Chart Train Data -->
          <div class="col d-flex align-items-center justify-content-center align-self-center">

            <div>
              <figcaption class="text-center">Train Data</figcaption>
              <canvas id="trainset" height="400" width="400"></canvas>
            </div>

          </div>
          <!-- Chart Validation Data -->
          <div class="col d-flex align-items-center justify-content-center align-self-center">

            <div>
              <figcaption class="text-center">Validation Data</figcaption>
              <canvas id="valset" height="400" width="400"></canvas>
            </div>

          </div>
        </div>

        <hr>

        <!-- Model Architecture -->
        <div>
          <ModelMaker
              :input-shape="inputShape"
              :output-shape="outputShape"
              :default-learning-rate="0.1"
              :default-epochs="100"
              :default-layers="[
                    {  type: 'dense',  units: 20,  activation: 'sigmoid'},
                    {  type: 'dense',  units: 1,  activation: 'sigmoid'}]"
              :default-loss="'metrics.binaryCrossentropy'"
              :default-optimizer="'sgd'"
              :metrics="['accuracy']"
              @trainModel="trainModel">
          </ModelMaker>
        </div>

        <!-- Messages -->
        <div>
          <Alert :text="statusMessage"></Alert>
          <div class="row">
            <div class="col">
              <Alert :text="eval_train"></Alert>
            </div>
            <div class="col">
              <Alert :text="eval_val"></Alert>
            </div>
          </div>
        </div>

        <!-- Graphs -->
        <Graph ref="graph"></Graph>

        <hr>

        <!-- Predict -->
        <div class="my-3">
          <h5>Predict</h5>

          <div class="form-group row">
            <label class="col-sm-2 col-form-label">X</label>
            <div class="col-sm-10">
              <input class="form-control" v-model="inputX">
            </div>
          </div>

          <div class="form-group row">
            <label class="col-sm-2 col-form-label">Y</label>
            <div class="col-sm-10">
              <input class="form-control" v-model="inputY">
            </div>
          </div>

          <Button text="Predict" :click="predict" :disabled="!modelTrained || !inputX || !inputY"></Button>
          <Alert :text="prediction"></Alert>
        </div>

      </div>
    </section>
    <!--endregion-->
  </div>
</template>

<script>
import TemplatePlayGround from '@/components/TemplatePlayGround'
import ModelMaker from '@/components/ModelMaker'
import Graph from "@/components/Graph";
import Button from '@/components/Button'
import Alert from '@/components/Alert'
import * as tf from '@tensorflow/tfjs'
import Chart from 'chart.js'

require('babel-polyfill')

export default {
  components: {
    TemplatePlayGround,
    ModelMaker,
    Graph,
    Button,
    Alert
  },
  data() {
    return {
      headerNav: 'PlayGround',
      sectionName: 'Binary Classification',
      sectionTitle: 'Binary Classification using TensorFlow.js',
      sectionDescription: `Binary Classification would generally fall into the domain of
      Supervised Learning since the training dataset is labelled. And as the name suggests
      it is simply a special case in which there are only two classes.
      Generally the activation function used is the sigmoid function.`,

      inputShape: [2],
      outputShape: 1,
      statusMessage: null,
      model: null,
      modelInitialized: false,
      modelTrained: false,
      eval_train: null,
      eval_val: null,

      inputX: null,
      inputY: null,
      prediction: null,

      ctx: null,
      scatterTrain: null,
      scatterVal: null
    }
  },
  methods: {
    // region Generate Data methods
    gaussianRand(a, b) {
      let rand = 0
      for (let i = 0; i < 6; i++) {
        rand += Math.random()
      }
      return (rand / 6) + (Math.random() * a + b)
    },
    toArray(arr) {
      const out = []
      for (let i = 0; i < arr.length; i++) {
        out.push([arr[i].x, arr[i].y])
      }
      return out
    },
    generateData(numPoints, frac) {
      // class proportion
      const nx1 = Math.round(numPoints * frac)
      const nx2 = numPoints - nx1

      // create random 2 dimension data
      const data1 = Array(nx1).fill(0).map(() => {
        return {x: this.gaussianRand(10, 7), y: this.gaussianRand(10, 7)}
      })
      const class1 = Array(nx1).fill(0)

      const data2 = Array(nx2).fill(0).map(() => {
        return {x: this.gaussianRand(9, 0), y: this.gaussianRand(9, 0)}
      })
      const class2 = Array(nx2).fill(1)

      // split 30% for data validation
      const nv1 = Math.round(nx1 * 0.7)
      const nv2 = Math.round(nx2 * 0.7)

      const trainPt = [data1.slice(0, nv1), data2.slice(0, nv2)]
      const valPt = [data1.slice(nv1), data2.slice(nv2)]

      const xTrain = this.toArray(trainPt[0].concat(trainPt[1]))
      const yTrain = class1.slice(0, nv1).concat(class2.slice(0, nv2))
      const xVal = this.toArray(valPt[0].concat(valPt[1]))
      const yVal = class1.slice(nv1).concat(class2.slice(nv2))

      return {
        xTrain, yTrain, xVal, yVal, trainPt, valPt
      }
    },
    // endregion
    async trainModel(model, batchSize, epochs, modelInitialized) {
      this.statusMessage = null
      try {
        this.model = model
        this.modelInitialized = modelInitialized
        this.statusMessage = 'Training, please wait...'

        await this.model.fit(this.xTrain, this.yTrain, {
          epochs: epochs,
          validationData: [this.xVal, this.yVal],
          callbacks: {
            onEpochEnd: async (epoch, logs) => {
              await this.$refs.graph.showGraph(logs)
            }
          }
        })

        const evalTrain = this.model.evaluate(this.xTrain, this.yTrain)
        const evalVal = this.model.evaluate(this.xVal, this.yVal)

        this.statusMessage = 'Training Done.'

        const round = (num) => parseFloat(num * 100).toFixed(2)
        this.eval_train = 'Train Accuracy : ' + round(evalTrain[1].dataSync()) + '%'
        this.eval_val = 'Validation Accuracy : ' + round(evalVal[1].dataSync()) + '%'
        this.modelTrained = true
      } catch (err) {
        this.model = null
        this.modelInitialized = false
        this.modelTrained = false
        this.statusMessage = err.message;
        console.log(err)
      }
    },
    predict() {
      const x = parseFloat(this.inputX)
      const y = parseFloat(this.inputY)

      this.scatterVal.data.datasets[2].data[0] = {x: x, y: y, r: 5}
      this.scatterVal.update()

      const yPred = this.model.predict(tf.tensor2d([[x, y]], [1, 2]))
      this.prediction = 'Predicted Class: ' + Math.round(yPred.dataSync())
    },
    prepare() {
      // CHART.JS VIS
      this.ctx = document.getElementById('trainset')
      this.scatterTrain = new Chart(this.ctx, {
        type: 'scatter',
        data: {
          datasets: [{
            data: this.dataset.trainPt[0],
            label: 'class 0',
            backgroundColor: 'black'
          }, {
            data: this.dataset.trainPt[1],
            label: 'class 1',
            backgroundColor: 'red'
          }]
        },
        options: {
          responsive: false
        }
      })

      this.ctx = document.getElementById('valset')
      this.scatterVal = new Chart(this.ctx, {
        type: 'scatter',
        data: {
          datasets: [{
            data: this.dataset.valPt[0],
            label: 'class 0',
            backgroundColor: 'black'
          }, {
            data: this.dataset.valPt[1],
            label: 'class 1',
            backgroundColor: 'red'
          }, {
            type: 'bubble',
            data: [],
            label: 'new data',
            backgroundColor: '#32fa32',
            borderColor: 'green'
          }]
        },
        options: {
          responsive: false
        }
      })
    }
  },
  computed: {
    dataset() {
      return this.generateData(100, 0.6)
    },
    xTrain() {
      return tf.tensor2d(this.dataset.xTrain, [this.dataset.xTrain.length, 2])
    },
    yTrain() {
      return tf.tensor2d(this.dataset.yTrain, [this.dataset.yTrain.length, 1])
    },
    xVal() {
      return tf.tensor2d(this.dataset.xVal, [this.dataset.xVal.length, 2])
    },
    yVal() {
      return tf.tensor2d(this.dataset.yVal, [this.dataset.yVal.length, 1])
    },
  },
  mounted() {
    this.prepare()
  }
}
</script>

